<!--同步课直播-->
<template>
  <div class="live">
    <div class="title">同步课直播</div>
    <div class="container">
      <div class="living">
        <div v-if="livingLists.length > 0" class="title">正在直播 {{livingLists.length}}</div>
        <div class="living-box">
          <div v-for="item in livingLists" :key="item.id" class="box-item-wrap">
            <div class="box-item">
              <div class="bg" @click="handleTo(item)">
                <div>
                  <i class="p-center iconfont icon-video"></i>
                </div>
              </div>
              <div class="info">
                <div class="title">
                  <span>{{item.userName}}</span>
                  <span>{{item.lookNumber}}</span>
                </div>
                <div class="author">
                  <span>{{item.name}}</span>
                  <span>{{item.orgName}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="notice">
        <div v-if="liveBroadlist.length > 0" class="title">直播预告 {{liveBroadlist.length}}</div>
        <div class="notice-box">
          <div v-for="item in liveBroadlist" :key="item.id" class="item-wrap">
            <div class="item">
              <div class="title">{{item.name}}</div>
              <div class="time">{{item.startTime}}</div>
              <div class="info">
                <div>{{item.userName}} {{item.orgName}}</div>
                <div>{{item.countDown}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    liveBox: () => import("@/components/live-box")
  },
  data() {
    return {
      livingLists:[],
      liveBroadlist:[]
    };
  },
  created(){
    this.geLivingata();
    this.getAptData();
  },
  methods: {
    geLivingata(){
      const params = {
        pageNo:1,
        pageSize:4,
        appointmentType:8002,
        createType:9070
      }
      this.$api.geLivingata(params).then(res => {
        this.livingLists = res.data;
      })
    },
    getAptData(){
      const params = {
        pageNo:1,
        pageSize:4,
        appointmentType:8002,
        createType:9070
      }
      this.$api.getAptData(params).then(res => {
        this.liveBroadlist = res.data;
      })
    },
    /* 跳转直播 */
    handleTo(item) {
      this.$router.push(`/liveCenter/video/play/${item.id}/${item.code}`)
    }
  }
};
</script>

<style lang="scss" scoped>
.live {
  padding: 32px;
  padding-left: 22px;
}
.live > .title {
  padding-left: 10px;
  color: #55595c;
  font-weight: bold;
  line-height: 1;
}
.container {
  margin-top: 20px;
}
.container .living {
  & > .title {
    margin-bottom: 10px;
    padding-left: 10px;
  }
  .living-box {
    display: flex;
    flex-wrap: wrap;
  }
  .living-box .box-item-wrap {
    width: 25%;
    padding: 0 10px;
  }
  .box-item-wrap .box-item {
    border-radius: 6px;
    overflow: hidden;
    position: relative;
    top: 0;
    transition: all 0.2s linear;
    &:hover {
      top: -10px;
    }
  }
  .box-item-wrap .bg {
    height: 0;
    padding-bottom: 70%;
    background: url("./../../../assets/image/city-1.jpg") no-repeat;
    background-size: 100% 100%;
    cursor: pointer;
    position: relative;
    &:hover {
      div {
        display: block;
      }
    }
    div {
      display: none;
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      top: 0;
      background: rgba(0, 0, 0, 0.2);
      i {
        font-size: 60px;
        color: #fff;
      }
    }
  }
  .box-item-wrap .info {
    background: #fff;
    padding: 10px;
  }
  .info > .title {
    display: flex;
    justify-content: space-between;
  }
  .info > .author {
    margin-top: 10px;
  }
  .info > .author > span {
    color: #909399;
    margin-right: 10px;
  }
}

.container .notice {
  margin-top: 30px;
  & > .title {
    margin-bottom: 10px;
    padding-left: 10px;
  }
  .notice-box {
    display: flex;
    flex-wrap: wrap;
  }
  .notice-box .item-wrap {
    width: 25%;
    padding: 0 10px;
    .item {
      cursor: pointer;
      border-radius: 6px;
      overflow: hidden;
      background: rgba(76, 133, 255, 0.8);
      height: 228px;
      padding: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .title {
        text-align: center;
        margin-top: 15%;
        color: #fff;
        font-size: 16px;
      }
      .time {
        color: #fff;
        background: rgba(0, 0, 0, 0.1);
        width: 160px;
        margin: 0 auto;
        border-radius: 15px;
        text-align: center;
        height: 30px;
        line-height: 30px;
      }
      .info {
        display: flex;
        justify-content: space-between;
        div {
          color: #fff;
        }
      }
    }
  }
}
</style>